import React, { useState } from 'react';
import { Form } from 'react-bootstrap';
import axios from 'axios';
import {
  Spinner
} from 'react-bootstrap';

import customToast from "./CustomToast";

const ROOT_PATH = process.env.REACT_APP_ROOT_PATH;


const ContactForm = () => {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [message, setMessage] = useState('');
  const [spinnerFlag, setSpinnerFlag] = useState(false);

  const sendMessage = (e) => {
    e.preventDefault();
    setSpinnerFlag(true);
    axios
      .post(`${ROOT_PATH}/send-mail`, { name, email, message })
      .then((resp = {}) => {
        setSpinnerFlag(false);
        const { message = '' } = resp.data;
        customToast.success(
          <div>
            {message}
          </div>
        );
        setName('');
        setEmail('');
        setMessage('');
      })
      .catch((error = { response: {} }) => {
        setSpinnerFlag(false);
        const { data = {} } = error.response;
        console.error('data', data);
        customToast.error(
          <div>
            {data.message}
          </div>
        );
      });
  };

  return (
    <div>
      <h2 className="text-center">Get In Touch</h2>
      <Form>
        <div class="form-row my-4">
          <div class="col-md-6 mb-3">
            <input
              type="text"
              class="form-control"
              placeholder="Name"
              value={name}
              onChange={(e) => {
                setName(e.target.value);
              }}
              required
            />
          </div>
          <div class="col-md-6 mb-3">
            <input
              type="email"
              class="form-control"
              placeholder="Email"
              value={email}
              onChange={(e) => {
                setEmail(e.target.value);
              }}
              required
            />
          </div>
        </div>
        <div class="form-row ">
          <div class="col-md-12 mb-3">
            <textarea
              class="form-control"
              rows="10"
              value={message}
              placeholder="Enter your message"
              required
              onChange={(e) => {
                setMessage(e.target.value);
              }}
            />
          </div>
        </div>
        <button
          type="submit"
          disabled={spinnerFlag}
          class="btn btn-dark  text-light btn-block"
          onClick={(e) => sendMessage(e)}
        >
          Send It
          {spinnerFlag && <span className='px-2'><Spinner animation="border" size="sm" /></span>}
        </button>

      </Form>
    </div>
  );
};

export const Contact = ({ contactRef }) => {
  return (
    <div id="contact_section" className="contact-section">
      <div className="container-fluid bg-portfolio py-5">
        <div className="row">
          <div className="col-12 col-sm-12 col-md-6 d-flex align-items-center">
            <div className="text-center">
              <img
                src="images/my-image.webp"
                alt="my image"
                className="img-fluid contact-profile-img rounded-circle"
              />
              <p className="text-light py-4">
                Looking for an experienced full-stack developer to build your
                web app or ship your software product? To start an initial chat,
                just drop me an email at{' '}
                <a
                  href="mailto:venkatesh.sv645@gmail.com"
                  className="text-light"
                >
                  <u>venkatesh.sv645@gmail.com</u>{' '}
                </a>{' '}
                or use the contact form
              </p>
            </div>
          </div>
          <div className="col-12 col-sm-12 col-md-6">
            <ContactForm contactRef={contactRef} />
          </div>
        </div>
      </div>
    </div>
  );
};
