import { toast } from "react-toastify";

const customToast = {
  info(msg) {
    return toast.info(msg, {
      className: "custom-toast-container custom-toast-info-container"
    });
  },
  success(msg) {
    return toast.success(msg, {
      className: "custom-toast-container custom-toast-success-container"
    });
  },
  error(msg) {
    return toast.success(msg, {
      className: "custom-toast-container custom-toast-error-container"
    });
  }
};

export default customToast;
